@keyframes fly-in-left {
    0% {
        transform: translateX(-100%); 
        filter:blur(5px);
        opacity: 0;
        
    }
    100% {
        transform: translateX(0);
        filter:blur(0);
        opacity: 100%;
    }
}

@keyframes fade-in{
    0%{
        opacity: 0;
        filter:blur(3px);
    }
    100%{
        opacity: 1;
        filter:blur(0);
    }
}
